import { FieldState } from "final-form";

export interface IValidator {
    validate: (
        value: string,
        allValues?: any,
        meta?: FieldState<string> | undefined
    ) => string | undefined;
}

export default class FormHelper {
    static validate(validators: IValidator[] = []) {
        return (
            value: string,
            allValues?: any,
            meta?: FieldState<string> | undefined
        ): string | undefined => {
            let error: any = undefined;
            validators.forEach((validator: IValidator) => {
                if (error) return;
                error = validator.validate(value, allValues, meta);
            });
            return error;
        };
    }
}
