import { FieldState } from "final-form";
import { IValidator } from "./FormHelper";

interface IEmailValidator {
    msg: string;
}

export class Email implements IValidator {
    msg: string = "";
    constructor({ msg }: IEmailValidator) {
        this.msg = msg;
    }

    template =
        /^([a-z0-9_-]+\.)*[a-z0-9_-]+@[a-z0-9_-]+(\.[a-z0-9_-]+)*\.[a-z]{2,6}$/;

    validate(val: string, _: any, meta: FieldState<string> | undefined) {
        const value = val && typeof val === "string" ? val.trim() : val;
        if (
            (!value || value === null || value === "" || value === undefined || !val.match(this.template)) &&
            meta?.visited
        ) {
            return this.msg;
        }
        return undefined;
    }
}
