import React from "react";
import { useDispatch } from "react-redux";
import { userActions } from "../../../redux/actions/user";
import { reactLocalStorage } from "reactjs-localstorage";
import { useTypedSelector } from "../../../hooks/useTypedSelectors";

export type AuthFormValues = {
    email: string;
    password: string;
    username?: string;
};

const useUser = () => {
    const dispatch = useDispatch();
    const [loggedIn, setLoggedIn] = React.useState(
        !!reactLocalStorage.get("token")
    );

    const username = useTypedSelector(({ user }) => user.username);

    const auth = React.useCallback(async (data: AuthFormValues) => {
        return userActions.authAction(data);
    }, []);

    const reg = React.useCallback(async (data: AuthFormValues) => {
        return userActions.regAction(data);
    }, []);

    const fetchInfo = React.useCallback(async () => {
        dispatch(userActions.fetchInfoAction());
    }, [dispatch]);

    return {
        reg,
        auth,
        fetchInfo,
        username,
        loggedIn,
        setLoggedIn,
    };
};

export default useUser;
