import React from "react";
import { Field, Form } from "react-final-form";
import { Link, useHistory } from "react-router-dom";
import { Button } from "../../components";
import { InputField } from "../../components/fields";
import useUser, { AuthFormValues } from "../Home/hooks/useUser";
import { reactLocalStorage } from "reactjs-localstorage";

import styles from "./style.module.scss";
import FormHelper from "../../utils/validators/FormHelper";
import { Email } from "../../utils/validators/EmailValidator";
import { Require } from "../../utils/validators/RequireValidator";
import { Range } from "../../utils/validators/RangeValidator";

type Props = {
    isReg: boolean;
};

export const setToken = (token: string, refresh_token: string) => {
    reactLocalStorage.set("token", token);
    reactLocalStorage.set("refresh_token", refresh_token);
};

const Auth: React.FC<Props> = ({ isReg = false }) => {
    const { reg, auth, setLoggedIn, loggedIn } = useUser();
    const history = useHistory();

    if (loggedIn) {
        history.push("/");
    }

    const authHandler = async (data: AuthFormValues) => {
        auth(data)
            .then(({ data }) => setToken(data.access_token, data.refresh_token))
            .then(() => setLoggedIn(true));
    };

    const regHandler = async (data: AuthFormValues) => {
        reg(data)
            .then(({ data }) => setToken(data.access_token, data.refresh_token))
            .then(() => setLoggedIn(true));
    };

    const onSubmit = async (data: AuthFormValues) => {
        const func = isReg ? regHandler : authHandler;
        func(data);
    };
    return (
        <div className={styles.auth}>
            <h1
                className={styles.auth_title}
                onClick={() => reactLocalStorage.clear()}
            >
                Pockets
            </h1>
            <h3 className={styles.auth_subtitle}>
                {isReg ? "Adventure starts here" : "Welcome to Pockets! üëãüèª"}
            </h3>
            <p className={styles.auth_description}>
                {isReg
                    ? "Make your app management easy and fun!"
                    : "Please sign-in to your account and start the adventure"}
            </p>
            <Form
                onSubmit={onSubmit}
                render={({ handleSubmit, errors }) => (
                    <form onSubmit={handleSubmit} className={styles.auth_form}>
                        {typeof errors === "object" && (
                            <ul className={styles.auth_form_errors}>
                                {Object.values(errors).map((error) => (
                                    <li
                                        className={styles.auth_form_errors_item}
                                    >
                                        {error}
                                    </li>
                                ))}
                            </ul>
                        )}
                        {isReg && (
                            <div className={styles.auth_field}>
                                <Field
                                    name="username"
                                    label="Usermame"
                                    render={(props) => (
                                        <InputField
                                            placeholder="John Doe"
                                            {...props}
                                        />
                                    )}
                                    validate={FormHelper.validate([
                                        new Require({
                                            msg: "–ò–º—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ",
                                        }),
                                        new Range({
                                            min: {
                                                count: 4,
                                                msg: "–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–∏–º–≤–æ–ª–æ–≤ –Ω–µ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –º–µ–Ω—å—à–µ 4",
                                            },
                                        }),
                                    ])}
                                />
                            </div>
                        )}
                        <div className={styles.auth_field}>
                            <Field
                                name="email"
                                label="Email"
                                render={(props) => (
                                    <InputField
                                        placeholder="johndoe@gmail.com"
                                        {...props}
                                    />
                                )}
                                validate={FormHelper.validate([
                                    new Require({
                                        msg: "Email –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ",
                                    }),
                                    new Email({ msg: "Email —É–∫–∞–∑–∞–Ω –Ω–µ –≤–µ—Ä–Ω–æ" }),
                                ])}
                            />
                        </div>
                        <div className={styles.auth_field}>
                            <Field
                                name="password"
                                type="password"
                                label="Password"
                                render={(props) => (
                                    <InputField
                                        placeholder="‚öâ ‚öâ ‚öâ ‚öâ ‚öâ ‚öâ ‚öâ ‚öâ"
                                        {...props}
                                    />
                                )}
                                validate={FormHelper.validate([
                                    new Require({
                                        msg: "–ü–∞—Ä–æ–ª—å –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ",
                                    }),
                                ])}
                            />
                        </div>
                        <Button>Login</Button>
                    </form>
                )}
            />
            <div className={styles.auth_bottom}>
                <p>
                    {isReg
                        ? "Already have an account?"
                        : "New on our platform?"}
                </p>
                {isReg ? (
                    <Link to="/auth">Sign in instead</Link>
                ) : (
                    <Link to="/reg">Create an account</Link>
                )}
            </div>
        </div>
    );
};

export default Auth;
