import React from "react";
import { map } from "lodash";
import { FieldInputProps } from "react-final-form";

import styles from "./style.module.scss";

type OptionType = {
    onClick: (option: any) => void;
    onClose: () => void;
    options: any[];
};

const Options: React.FC<OptionType> = ({ options, onClick, onClose }) => {
    const optionsRef = React.useRef(null);

    const onCloseHandler = React.useCallback(() => {
        onClose();
    }, [onClose]);

    React.useEffect(() => {
        document.body.addEventListener("click", onCloseHandler);

        return () => document.body.removeEventListener("click", onCloseHandler);
    }, [onCloseHandler]);
    return (
        <div className={styles.select_options} ref={optionsRef}>
            {map(options, (option, index) => (
                <div
                    key={`${option.value}_${index}`}
                    onClick={() => onClick(option)}
                    className={styles.select_options_item}
                >
                    {option.value}
                </div>
            ))}
        </div>
    );
};

type Props = {
    fieldProps: FieldInputProps<any>;
    options: any[];
    placeholder?: string;
};

const SelectField: React.FC<Props> = ({
    fieldProps,
    options = [],
    placeholder,
}) => {
    const [showOptions, setShowOptions] = React.useState(false);

    const showOptionsHandler = () => {
        setShowOptions((prev) => !prev);
    };

    const closeHandler = () => {
        setShowOptions(false);
    } 

    const onClickHandler = (option: any) => {
        fieldProps.onChange(option);
        closeHandler()
    };

    return (
        <div className={styles.select}>
            <p className={styles.select_value} onClick={showOptionsHandler}>
                {fieldProps.value.value ||
                    placeholder ||
                    (options.length && options[0].value)}
            </p>
            {showOptions && (
                <Options onClick={onClickHandler} options={options} onClose={closeHandler}/>
            )}
        </div>
    );
};

export default SelectField;
