import { FieldState } from "final-form";
import { IValidator } from "./FormHelper";

interface IRequireValidator {
    msg: string;
}

export class Require implements IValidator {
    msg: string = "";
    constructor({ msg }: IRequireValidator) {
        this.msg = msg;
    }

    validate(val: string, _: any, meta: FieldState<string> | undefined) {
        const value = val && typeof val === "string" ? val.trim() : val;
        if ((!value || value === null || value === "" || value === undefined) && meta?.visited) {
            return this.msg;
        }
        return undefined;
    }
}
