import { FieldState } from "final-form";
import { IValidator } from "./FormHelper";

type Test = {
    msg: string;
    count: number;
};

interface IRangeValidator {
    max?: Test;
    min?: Test;
}

export class Range implements IValidator {
    max: Test | undefined;
    min: Test | undefined;
    constructor({ max, min }: IRangeValidator) {
        this.max = max;
        this.min = min;
    }

    validate(val: string, _: any, meta: FieldState<string> | undefined) {
        const value = val && typeof val === "string" ? val.trim() : val;
        if (meta?.visited) {
            if (this.max && value && value.length > this.max.count) {
                return this.max.msg;
            }
            if (this.min && value && value.length < this.min.count) {
                return this.min.msg;
            }
            if (!value && (this.min || this.max)) {
                // TODO: –ø–µ—Ä–µ–∫–∏–Ω—É—Ç—å –Ω–∞–≤–µ—Ä—Ö
                if (this.min) {
                    return this.min.msg;
                }
                if (this.max) {
                    return this.max.msg;
                }
            }
        }

        return undefined;
    }
}
